import React, {useState, useEffect} from 'react'
import { Comment, Avatar, Form, Button, List, Input } from 'antd';
import Url from '../../../../../../url/url.js';
import moment from 'moment';
import {swallErr} from './../../../../../../alert/alert.js'
import localStorage_change from './../../../../../../#localStorage_change.js'

// Style 
import styled from '@emotion/styled';

const Chat = ({notesGlobal, update, _id}) => {

	const { TextArea } = Input;

	const CommentList = ({ comments }) => (
		<List
			dataSource={comments}
			header={`${comments.length} ${comments.length > 1 ? 'replies' : 'reply'}`}
			itemLayout="horizontal"
			renderItem={props => <Comment {...props} />}/>
	);

	useEffect(() => {
		document.querySelector('.ant-input').value = ''
		let block = document.querySelector('.ant-drawer-body')
  		block.scrollTop = block.scrollHeight
	})

	// ===========================================================================================================================

	let {surname, name, midname} = require('jsonwebtoken').decode(localStorage.getItem('tokenID'))

	let UserName = `${surname} ${name} ${midname}`

	const [comments, setComments] = useState(notesGlobal)
	const [submitting, setSubmitting] = useState(false)
	let value = ''

	const axios = require('axios'); // AJAX

	console.log(moment().locale('ru').format("dddd, D MMMM YYYY Ð³., HH:mm"))

	const handleSubmit = () => {
		if (!value) {
			return;
		}

		setSubmitting(true)

		axios({
			method: 'post',
			url: `${Url}/CRM/Pupils/${_id}/Notes`,
			headers: {
				'Content-Type': 'application/json',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			},
			data: {
				text: value
			}
		})
		.then(() => {

			setSubmitting(false)
			setComments([...comments, {
				author: UserName,
				content: <p>{value}</p>,
				datetime: moment().locale('ru').format("dddd, D MMMM YYYY Ð³., HH:MM"),
			}])
			// update()
		})
		.catch((error) => {
			if (error.response) {
				let {status, data} = error.response;

				if (status == 404) {
					swallErr('404', 'Ð¡ÐµÑÐ²ÐµÑ Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½')
				} else if (status == 401) {

					if (data.message == 'TOKEN_EXPIRED') {

						localStorage_change(data.token);
						value = value
						handleSubmit();
					} else {
						localStorage.removeItem('tokenID');
						window.location.replace("/");
					}
				}	
			}
		})
	};



    return (
      <>
        {comments.length > 0 && <CommentList comments={comments} />}
        	<Comment
	          content={
	          	<>
		            <Form.Item>
						<TextArea rows={4} onChange={(e) => {value = e.target.value}} placeholder='ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ ÐºÐ¾Ð¼Ð¼ÐµÐ½ÑÐ°ÑÐ¸Ð¹'/>
					</Form.Item>
					<Form.Item>
						<Button htmlType="submit" loading={submitting} onClick={handleSubmit} type="primary">
							ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ ÐºÐ¾Ð¼Ð¼ÐµÐ½ÑÐ°ÑÐ¸Ð¹
						</Button>
					</Form.Item>
				</>
	          }
	        />
      </>
    );
}

export default Chat