import React, {useState, useEffect} from 'react';

import { Table, Tag, Space, Pagination } from 'antd';


const TableGroups = ({Groups, newGroup, setGROUP, GROUP}) => {

	const { Column, ColumnGroup } = Table

	const data = Groups.map((item, i) => {

		let {_id, GROUP_NAME, GLOBAL_SCHEDULE} = item.group,
			color = 'cyan'

			if (GROUP.group) {
				if (newGroup.group._id == _id & !GROUP.group.GROUP_NAME) {

					color = 'volcano'
				} else if (_id == GROUP.group._id) {

					color = 'volcano'
				}
			}

		return {
			key: i,
			groupName: { 
				GROUP_NAME: GROUP_NAME,
				G: item
			},
			date: {
				date: `${GLOBAL_SCHEDULE[`${item.indexD}`].date} Ð² ${GLOBAL_SCHEDULE[0].duration[0]}`,
				color: color,
				G: item
			}
		}
	})

	let pagination = {
		pageSize: 15,
		onChange: (i) => {
			console.log(`Ð¡ÑÑÐ°Ð½Ð¸ÑÐ°: ${i}`)
		}
	}

	return (
	  <Table 
	  	pagination={pagination}
	  	loading={false}
	  	scroll={{ x: 0 }}

	  	dataSource={data}>
	  		<Column
	  		title="ÐÑÐµÐ¼Ñ"
	  		dataIndex="date"
	  		key="date"
	  		render={(date) => (
	  			<Tag
	  				onClick={() => {setGROUP(date.G)}} 
	  				style={{cursor: 'pointer'}}
	  				color={date.color}>{date.date}</Tag>
	  		)}/>

	  		<Column
	  		title="ÐÑÑÐ¿Ð¿Ð°"
	  		dataIndex="groupName"
	  		key="groupName"
	  		render={(groupName) => (
	  			<Tag 
	  				onClick={() => {setGROUP(groupName.G)}}
	  				style={{cursor: 'pointer'}}
	  				color="volcano">{groupName.GROUP_NAME}</Tag>
	  		)}/>
	  </Table>
	)
}


TableGroups.defaultProps = {
	Groups: [{
			GROUP_NAME: '',
			GLOBAL_SCHEDULE: [
				{date: "", duration: ['', '']}
			],
			PUPILS: []
		}]
}

export default TableGroups