import React, { useState } from 'react';
import { Drawer, Button } from 'antd';

import ChatItem from './chat-item/chat.js'

// Style 
import styled from '@emotion/styled';

const DrawerChat = ({notes, update, _id}) => {
  const [visible, setVisible] = useState(false);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    if (visible) {
          update()
        }
    setVisible(false);
  };

  const COMEN = styled.div({
    '.ant-comment-content-detail': {
      borderBottom: '1px solid #f0f0f0'
    }
  })

  let notesGlobal = []
  notes.forEach(item => {

    let {author, date, text} = item

    notesGlobal.push(
    {
      author: author,
      content: <p>{text}</p>,
      datetime: date,
    }
    )
  })

  return (
    <>
      <Button className='Button-Drawer' type="primary" onClick={showDrawer}>
        ÐÑÐºÑÑÑÑ ÐºÐ¾Ð¼ÐµÐ½ÑÐ°ÑÐ¸Ð¸
      </Button>
      <Drawer
        width={'400px'}
        placement="right"
        closable={false}
        onClose={onClose}
        visible={visible}
      >
      <COMEN>
        <ChatItem notesGlobal={notesGlobal} update={update} _id={_id}/>
      </COMEN>
      </Drawer>
    </>
  );
};

export default DrawerChat