import React, {useState, useEffect} from 'react';

import Schedule from './../../#schedule/schedule.js';
import ScheduleForm from './../../#schedule/form/scheduleForm.js';
import PupilsSandTime from './../group-page/PupilsSandTime/PupilsSandTime.js';
import AddStydentGroup from './../../#more-functions/add-student_group/add-Student_Group.js';
import InfoTable from './Info-table/InfoTable.js';
import PayHistory from './payment-history/paymentHistory.js';

import moment from 'moment';

import Url from './../../../../url/url.js';
import localStorage_change from './../../../../#localStorage_change.js';
// Style 
import './group-page.css';
import styled from '@emotion/styled';

const GroupPage = ({dataGroup, setUrlPage}) => {

	const [DataALL, setDataALL] = useState({
		  	PUPILS: [
		  		{   
		  			_id: '564564',
		  			localSchedule: {}
		  		}
		  	],
		  	GROUP_NAME: "ÐÐ°Ð³ÑÑÐ·ÐºÐ°...",
			TUTOR: "",
			LEVEL: '',
			PLACES: '',
			GLOBAL_SCHEDULE: [
				{
					duration: ['--:--', '--:--']
				}
			],
			_id: '6456'
	});

 	const {GROUP_NAME, TUTOR, LEVEL, PLACES, PUPILS, GLOBAL_SCHEDULE, _id} = DataALL; 

 	let arrPayHistory = [];
 	PUPILS.forEach((item) => {

 		if (item.paymentHistory) {
 			item.paymentHistory.forEach((itemLocal) => {
 				if (itemLocal.group_id == _id) {
 					arrPayHistory.push(itemLocal);
 				}
 			});
 		}
 	});

 	// let SCHEDULE = [
 	// 	{date: "01.01.2021", duration: ['10:00', '11:00'], status: "", tutor: ""},
	 //    {date: "04.01.2021", duration: ['10:00', '11:00'], status: "", tutor: ""},
	 //    {date: "06.01.2021", duration: ['10:00', '11:00'], status: "", tutor: ""}
 	// ];

	const axios = require('axios'); // AJAX

	const getGroup_Id = () => {
		axios({
			method: 'get',
			url: `${Url}/CRM/Groups/${dataGroup}`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			}
		})
		.then((res) => {
			setDataALL(res.data);
		})
		.catch((error) => {
			if (error.response) {
				if (error.response.status == 401) {

					if (error.response.data.message == 'TOKEN_EXPIRED') {

						localStorage_change(error.response.data.token);
						getGroup_Id();
					} else {
						localStorage.removeItem('tokenID');
						window.location.replace("/");
					}
				}	
			}
		});
	}

	useEffect(() => {
		getGroup_Id();
	}, []);

	const ChangeGroup = () => {
		// Ð ÐµÐ´Ð°ÐºÑÐ¸ÑÐ¾Ð²Ð°Ð½Ð¸Ðµ Ð³ÑÑÐ¿Ð¿Ñ. 
	}

	const breakpoints = [767],
		  mq = breakpoints.map(
			bp => `@media (max-width: ${bp}px)`
		);

	const GroupPage = styled.div({
		marginTop: '0px',
		margin: '0 auto',
		'.pad': {
			padding: '0px',
			paddingRight: '10px',
			[mq[0]]: {
				paddingRight: '0px'
			}
		},
		'.infoLog': {
			marginBottom: '10px',
			padding: '0',
			[mq[0]]: {
				paddingBottom: '10px'
			},
			'.calendar': {
				margin: '9px 0',
    			marginRight: '10px',
    			fontSize: '15px'
			}
		},
		'.group-name': {
			padding: '0',
			borderBottom: '1px solid #0dcaf0',
			[mq[0]]: {
				borderRight: 'none'
			}
		},
		'.time': {
			margin: '0',
			padding: '0',
			h5: {
				fontSize: '1.25rem'
			}
		},
		'.ulBox': {
			borderRadius: '15px',
			padding: '0px'
		},
		'.editing': {
			background: '#fff',
    		borderRadius: '10px 10px 0 0',
    		i: {
    			marginLeft: '95%',
    			cursor: 'pointer'
    		}
		},
		li: {
			backgroundColor: 'none',
			span: {
				fontSize: '16px',
				margin: '0 5px 5px 0'
			}
		}
	});
	const TablePupils = styled.div({
		background: '#fff',
	    margin: '10px 0',
	    padding: '10px',
	    borderRadius: '15px',
		'& > h3': {
			fontSize: '22px'
		}
	});

	let PUPILS_List = PUPILS.map((item) => {
		return (
			<span key={item._id} className="badge bg-info text-dark">{`${item.name} ${item.surname}`}</span>
		);
	});

	let PUPILSandTIME = PUPILS.map((itemG) => {
		return <PupilsSandTime key={itemG._id} setUrlPage={setUrlPage} itemG={itemG} dataGroup={dataGroup} getGroup_Id={getGroup_Id} surname={itemG.surname} name={itemG.name}/>;
	});

	let jwt = require('jsonwebtoken')

	let addPupils_g = ''
	if (jwt.decode(localStorage.getItem('tokenID')).role != 'teacher') {
		addPupils_g = <AddStydentGroup 
						PupilsGroup={PUPILS} 
						schedule={GLOBAL_SCHEDULE} 
						update={getGroup_Id} 
						pageId={dataGroup} 
						http='/CRM/Pupils' 
						title='ÑÑÐµÐ½Ð¸ÐºÐ°'
						/>
	}

	return (
		<>
		<GroupPage className="container row col-12">
				<div className="col-12 col-md-6 pad">
					<div className="infoLog align-self-start col-md-12 col-12">
						<div className="group-name col-12 col-lg-12">
							<h3>{GROUP_NAME}</h3>
						</div>
						<div className="time col-md-12 row">
							<h6 className="calendar badge bg-info text-dark">Ð Ð°ÑÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ: </h6>
							{/*{calendarPlus}*/}
							<ScheduleForm updateGroup={getGroup_Id} groupID={dataGroup}/>
							<Schedule GLOBAL_SCHEDULE={GLOBAL_SCHEDULE}/>
						</div>
						{/*<PayHistory pay_History={arrPayHistory}/>*/}
					</div>
				</div>
				<div className="col-md-6 ulBox">
					<ul className="list-group list-group-flush">
						<InfoTable Data_ALL={DataALL} updetePage={setDataALL}/>
						<li className="list-group-item">Ð£ÑÐµÐ½Ð¸ÐºÐ¸: {PUPILS_List} {addPupils_g}</li>
					</ul>
				</div>
				<TablePupils className="container col-12">
					<h3 className="badge bg-warning">Ð£ÑÐµÐ½Ð¸ÐºÐ¸:</h3>
					{PUPILSandTIME}
				</TablePupils>
		</GroupPage>
		</>
	);
};
export default GroupPage;