import React, {useState, useEffect} from 'react';
import { Menu, Dropdown } from 'antd';

import Url from './../../../../../url/url.js';
import localStorage_change from './../../../../../#localStorage_change.js';
import {swallGood, swallErr} from './../../../../../alert/alert.js';

import Change_Balance from './../../../change-balance/changeBalance.js';

import moment from 'moment';
import {Link} from "react-router-dom";
// Style 
import styled from '@emotion/styled';

const PupilsSandTime = ({itemG, dataGroup, getGroup_Id, surname, name, setUrlPage, pageInfo}) => {

	const axios = require('axios'); // AJAX

	const [visibleNumber, setVisibleNumber] = useState('-1')
	// const [trigger, setTrigger] = useState('click')

	const [fixedSchedule, setFixedSchedule] = useState(['fixedSchedule-H', '', 'bi-plus-circle']);

	const [dataS, setDataS] = useState(itemG.localSchedule[`${dataGroup}`]);
	// const [dataS, setDataS] = useState([]);

	// console.log(itemG)

	const revers = () => {
		if (fixedSchedule[0] == 'fixedSchedule-H') {
			setFixedSchedule(['scheblueFixet', ' global-fon3000', 'bi-x-circle']);
		} else {
			setFixedSchedule(['fixedSchedule-H', '', 'bi-plus-circle']);
		}
	}

	const addNotes = (text) => {
		axios({
			method: 'post',
			url: `${Url}/CRM/Pupils/${itemG._id}/Notes`,
			headers: {
				'Content-Type': 'application/json',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			},
			data: {
				text: text
			}
		})
		.then(() => {
			getGroup_Id()
		})
	}

	const [Abic, setAbic] = useState([]);

	const getAbic = () => {
		axios({
	        method: "get",
	        url: `${Url}/CRM/Subscriptions`,
	        headers: {
	            "Content-Type": "application/json;charset=utf-8",
	            Authorization: `Bearer ${localStorage.getItem("tokenID")}`,
	        },
		})
		.then((res) => {
			setAbic(res.data);
		})
		.catch((error) => {
			if (error.response) {
				let { status, data, statusText } = error.response;

				if (status == 404) {
				    swallErr("Error 404", "Ð¡ÐµÑÐ²ÐµÑ Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½");
				} else if (status == 401) {
				    if (data.message == "TOKEN_EXPIRED") {
				        localStorage_change(error.response.data.token);
				        getGroup_Id();
				    } else {
				        localStorage.removeItem("tokenID");
				        window.location.replace("/");
				    }
				}
			}
		});
	}

	const axiosPut = (mass) => {
					axios({
						method: 'put',
						url: `${Url}/CRM/Groups/${dataGroup}/Pupils/${itemG._id}/Schedule`,
						headers: {
							'Content-Type': 'application/json;charset=utf-8',
							'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
						},
						data: mass
					})
					.then((res) => {
					})
					.catch((error) => {
						if (error.response) {
							let {status, data, statusText} = error.response;

							if (status == 404) {
								
								swallErr('Error 404', 'Ð¡ÐµÑÐ²ÐµÑ Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½');

							} else if (status == 401) {

								if (data.message == 'TOKEN_EXPIRED') {

									localStorage_change(error.response.data.token);
									getGroup_Id();
								} else {
									localStorage.removeItem('tokenID');
									window.location.replace("/");
								}
							}	
						}
					});
	}

	const liAbic = Abic.map((item, i) => {
		const PaySubscription = () => {
			let arr = [...dataS];
			
			let n = 0,
				index = 0;
			while (n < item.hoursCount) {
				if (!arr[index].paid) {
					arr[index].paid = true;
					n++;
				}
				index++;
			}

			let updete = () => {}; // ÐÐµ Ð¾Ð±ÑÐ°ÑÑÐ¹ Ð²Ð½Ð¸Ð¼Ð°Ð½Ð¸Ðµ.
			Change_Balance(`-${item.price}`, itemG._id, updete, getGroup_Id, item.name, dataGroup);

			axiosPut(arr);
			setDataS(arr);
		}
		return (
			<Menu.Item key={i} className="li">
				<span 
					onClick={PaySubscription}
					className="dropdown-item bg-info text-light" 
					href="#">
					{item.name}
				</span>
			</Menu.Item>);
	});

	const menu = (
		<Menu>
			{liAbic}
		</Menu>
	)

	useEffect(() => {
		getAbic();
	}, []);

	const breakpoints = [780, 1380, 2002], // 619, 1238, 1857, > 2476
		mq = breakpoints.map(
			bp => `@media (max-width: ${bp}px)`
		),
		mqMin = breakpoints.map(
			bp => `@media (min-width: ${bp}px)`
		);


	const Div_box = styled.div({
			borderBottom: '1px solid #91A8B0',
			paddingBottom: '5px',
			marginBottom: '5px',
			i: {
				color: '#17a2b8', 
				fontSize: '26px', 
				cursor: 'pointer',
				paddingTop: '10px'
			},
			'.global-fon3000': {
			    position: 'fixed',
			    top: '0',
			    left: '0',
			    zIndex: '99',
			    height: '100vh',
			    background: 'rgba(0, 0, 0, 0.6)',
			    overflowY: 'scroll',
			    i: {
			    	position: 'fixed',
				    top: '3px',
				    left: '7px',
				    color: '#fff'
			    }
			},
			h4: {
				display: 'inline-block',
			    marginRight: '10px',
			    marginBottom: '19px'
			},
			'.fixedSchedule-H': {
				maxHeight: '81px',
			    overflow: 'hidden'
			},

			'.scheblueFixet': {
			    margin: '0 auto',
			    marginTop: '50px',
			    width: '86%'
			},
			'.fixedSchedule-H, .scheblueFixet': {
				overflow: 'hidden',
				width: '2476px',
					[mq[2]]: {
						width: '1857px'
					},
					[mq[1]]: {
						width: '1238px'
					},
					[mq[0]]: {
						width: '619px'
					},
			},
			'.boxDay': {
				overflowX: 'auto',
				padding: '0 0 10px 0',
				'&::-webkit-scrollbar': {
					height: '10px',
				    backgroundColor:'rgba(0, 0, 0, 0.5)',
				    borderRadius: '10px'
				},
				'&::-webkit-scrollbar-thumb': {
					backgroundColor: '#17a2b8',
					borderRadius: '10px'
				}
			}
	});

	let Span = styled.div({ // ÐÐµÐ½Ñ Ð¸Ð· ÑÐ°ÑÐ¿Ð¸ÑÐ°Ð½Ð¸Ñ ÑÑÐµÐ½Ð¸ÐºÐ°.
		margin: '0 3px 0 0',
		position: 'initial',
	    button: {
	    	fontSize: '15px',
	    	fontWeight: '600',
	    	padding: '1px 7px',
	    	width: '71px'
	    },
	    '.btnAbic': {
	    	width: '100%'
	    },
	    ul: {
	    	border: 'none',
	    	background: 'rgba(0, 0, 0, 0.3)',
			padding: '6px'
	    },
	    '.li': {
	    	border: 'none',
	    	background: 'rgba(0, 0, 0, 0)',
	    	span: {
	    		borderRadius: '6px',
	    		fontWeight: '500',
	    		cursor: 'pointer'
	    	},
	    	'.input-group-text': {
	    		margin: '0',
	    		borderRadius: '6px 0 0 6px'
	    	},
	    	textarea: {
	    		padding: '2px 7px',
	    		fontWeight: '500'
	    	}
	    }
	});

		let SchedulePupils;

		if (dataS) {

			let index = 1;

			SchedulePupils = dataS.map((item, i) => {
				let marginRight = 0;
				if (index == 8) {
					marginRight = 27;
					index = 1;
				} else {
					index++;
				}

				let day = moment(item.date, 'DD.MM.YYYY').format('DD.MM');

				let color = '#f8f9fa',
					opacity = '1';

				let time = moment(item.date, 'DD.MM.YYYY').format('YYYY-MM-DD'),
					time_day = moment().diff(time, 'day');

				if (item.paid & item.status != 3 & time_day <= 0) {
					color = '#17a2b8';
				} else if (item.status == 3 & time_day <= 0) {
					color = '#6c757d'
				} else if (item.status == 3 & item.paid) {
					color = '#dc3545';
				} else if (time_day > 0 & item.paid) {
					color = '#28a745';
				} else if (time_day > 0 & !item.paid & item.status != 3) {
					color = '#28a745';
					opacity = '0.5';
				} else if (time_day > 0 & !item.paid & item.status == 3) {
					color = '#dc3545';
					opacity = '0.5';
				} 

				const changeDayIndex = (index) => {
					let arr = [...dataS];

					if (index == 2 & arr[i].status == 3) {
						arr[i].status = index;

						axiosPut(arr);
						setDataS(arr);

					} else if (index == 3 & time_day < 0) {
						arr[i].status = index;

						axiosPut(arr);
						setDataS(arr);
					} else if (index != 3 & index !=2) {
						arr[i].title = index;
						addNotes(index)
						axiosPut(arr);
						setDataS(arr);
					} else {
						swallErr('ÐÐµ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ ÑÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ ÑÑÐ°ÑÑÑ');
					}
				};

				const Item = styled.span({
					marginRight: `${marginRight}px`
				})
				let title = '';
				let colorTitle = '';
				if (item.title) {
					colorTitle = '#ffc107';
				}

				const menuItem = (
					<Menu key={day}>
						<Menu.Item key={`${day}1`}>
							<span onClick={() => {changeDayIndex('3')}} className="dropdown-item bg-secondary text-light" href="#">ÐÑÐ¾Ð¿ÑÑÑÐ¸Ð»</span>
						</Menu.Item>
						<Menu.Item key={`${day}2`}>
							<span onClick={() => {changeDayIndex('2')}} className="dropdown-item bg-success text-light" href="#">Ð£ÑÐ¾Ðº Ð¿ÑÐ¾Ð¹Ð´ÐµÐ½</span>
						</Menu.Item>
						<Menu.Item key={`${day}3`}>
							<div className="input-group">
								<span 
									onClick={() => {
										changeDayIndex(title)
										setVisibleNumber('')
									}}
									className="input-group-text bg-info text-light"
									>
									Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ
								</span>
								<textarea 
									placeholder='ÐÑÑÐ°Ð²ÑÑÐµ Ð·Ð°Ð¼ÐµÑÐºÑ...'
									defaultValue={item.title}
									onChange={(e) => {title = e.target.value}}
									className="form-control" aria-label="With textarea"
								></textarea>
							</div>
						</Menu.Item>
					</Menu>
				)

				let visibleLocal = false

				if (visibleNumber === i) {
					visibleLocal = true
				} else if (visibleNumber === '') {
					visibleLocal = false
				}
 
				return (
					<Item key={`${day}${i}`}>
						<Span className="btn-group">
							<Dropdown 
								onVisibleChange={(s) => {
									// if (visibleNumber === i) {
									// 	setVisibleNumber('')
									// } else {
									// 	setVisibleNumber(i)
									// }
									console.log(s)
								}}
								overlay={menuItem} 
								visible={visibleLocal} 
								trigger={['click']}
								>
								<button 
									type="button" 
									style={{color: `${colorTitle}`, background: `${color}`, opacity: `${opacity}`}} 
									className={`btn dropdown-toggle`}
									onClick={() => {
										if (visibleNumber === i) {
											setVisibleNumber('')
										} else {
											setVisibleNumber(i)
										}
									}}
									>
									{day}
								</button>
							</Dropdown>
						</Span>
					</Item>	
				);
			});
		};

		let urlPage = itemG._id,
			page = 'student';
		if (pageInfo) {
			page = pageInfo;
			urlPage = dataGroup;
		}
		let LinkItem = (
			<Link
				onClick={() => {setUrlPage(urlPage)}}
				to={`/${page}/${urlPage}`}>
				<h4>{`${surname} ${name}`}</h4>
			</Link>
		);

		let oplata = ''

		let jwt = require('jsonwebtoken');
		if (jwt.decode(localStorage.getItem('tokenID')).role != 'teacher') {
			oplata = (
				<span>
					<Span className="btn-group">
						<Dropdown overlay={menu} trigger={['click']}>
							<button 
								type="button" 
								className={`btn btn-success dropdown-toggle btnAbic`} 
								>
								ÐÐ¿Ð»Ð°ÑÐ¸ÑÑ
							</button>
						</Dropdown>
					</Span>
				</span>	
			)
		}

		return (
			<Div_box key={itemG._id} className="table">
				<div className="pupils-item row">
					<div className="col-12">
						{LinkItem}
						{oplata}
					</div>
					<div className={`col-12${fixedSchedule[1]}`}>
						<div className="boxDay">
							<div className={`${fixedSchedule[0]}`}>
								{SchedulePupils}
							</div>
						</div>
						<i onClick={() => {revers()}} className={`bi ${fixedSchedule[2]}`}></i>
					</div>
				</div>
			</Div_box>
		);
};

PupilsSandTime.defaultProps = {
	surname: ''
}

export default PupilsSandTime;