let jwt = require('jsonwebtoken');

const CheckRole = () => {
	if (jwt.decode(localStorage.getItem('tokenID')).role != 'teacher') {
		return true
	}
	return false
}

export let Display = ''

if (localStorage.getItem('tokenID')) {
	if (jwt.decode(localStorage.getItem('tokenID')).role = 'teacher') {
		console.log(jwt.decode(localStorage.getItem('tokenID')))
		Display = ' displayNone'
	}
}

export default CheckRole;