import React, {useState, useEffect} from 'react';

import localStorage_change from '../../../../#localStorage_change.js';
import Url from '../../../../url/url.js';
// import {swallGood, swallErr} from './../../../../alert/alert.js';

import styled from '@emotion/styled';

const Subscriptions = () => {

	const axios = require('axios'); // AJAX

	const [subscriptions, setSubscriptions] = useState([]);

	const getSubscriptions = () => {
	    axios({
	        method: "get",
	        url: `${Url}/CRM/Subscriptions`,
	        headers: {
	            "Content-Type": "application/json;charset=utf-8",
	            Authorization: `Bearer ${localStorage.getItem("tokenID")}`,
	        },
	    })
	        .then((res) => {
	            setSubscriptions(res.data);
	        })
	        .catch((error) => {
	            if (error.response) {
	                if (error.response.status == 401) {
	                    if (error.response.data.message == "TOKEN_EXPIRED") {
	                        localStorage_change(error.response.data.token);
	                    	getSubscriptions();
	                    } else {
	                        localStorage.removeItem("tokenID");
	                        window.location.replace("/");
	                    }
	                }
	            }
	        });
	};

	useEffect(() => {
		getSubscriptions();
	}, []);



	let subscriptions_box = subscriptions.map((item, i) => {

		let {name, price, daysCount} = item;

		return (
			<div key={i} className="form-check badge bg-warning text-dark">
	            	<input
		                onChange={(e) => {}}
		                className="input-check"
		                value={name}
		                type="checkbox"
		            />
	            	<label className="form-check-label">{name}</label>
	            
	        </div>
		);
	});

	// Style
	const Subs_ons = styled.div({
		padding: '0px',
		input: {
			margin: '0'
		},
		'.form-check': {
			marginLeft: '6px',
		    marginBottom: '5px',
		    width: '179px',
		    textAlign: 'left',
			label: {
				padding: '2px',
			    margin: '4px 0 2px 2px',
			    fontSize: '15px',
			}
		}
	});


	return (
	<Subs_ons className="col-12 row">
	    <label className="form-label col-md-4">ÐÑÐ¸ÐºÑÐµÐ¿Ð¸ÑÑ Ð°Ð±Ð¾Ð½ÐµÐ¼ÐµÐ½ÑÑ:</label>
	    <div className="col-md-7">
	       	{subscriptions_box}
	    </div>
	</Subs_ons>
	);
}
export default Subscriptions;