import React, {useState, useEffect} from 'react';
import {BrowserRouter as Router, Route, Link} from "react-router-dom";

import {loading, Swalclose} from './../../alert/alert.js'
import Url from './../../url/url.js'
import localStorage_change from './../../#localStorage_change.js'

import Today from './today/today.js'
import Ð¡alendar from './calendar/calendar.js'

import AllPupils from './all-pupils/all-pupils.js'
import AllGroups from './all-groups/all-groups.js'

import { Layout, Menu } from 'antd';
import {
	LogoutOutlined,
	ClockCircleOutlined,
	CalendarOutlined,
	TeamOutlined,
	UserOutlined,
} from '@ant-design/icons';

import styled from '@emotion/styled';

const HomePageTutor = ({logOut}) => {

	const onError = (error, fnc) => {
		if (error.response) {
			if (error.response.status == 401) {

				if (error.response.data.message == 'TOKEN_EXPIRED') {

					localStorage_change(error.response.data.token);
					fnc();
				} else {

					localStorage.removeItem('tokenID');
					window.location.replace("/");
				}
			}	
		}
	}

	const axios = require('axios'); // AJAX
	let jwt = require('jsonwebtoken');

	const [Groups, setGroups] = useState([])

	let Pupils = []
	Groups.forEach(item => {
		Pupils.push(...item.PUPILS)
	})

	console.log(Groups, Pupils)

	const [user, setUser] = useState({
		groups: [],
		login: "vika",
		midname: "ÐÐ»ÐµÐ³Ð¾Ð²Ð½Ð°",
		name: "ÐÐ¸ÐºÐ°",
		role: "teacher",
		surname: "ÐÐ»ÐµÐ³Ð¾Ð²Ð½Ð°",
		_id: "60d9e"
	})



	const getGroups = (l_user) => {

		let mass_id = [];
		l_user.groups.forEach((item, i) => {
			mass_id.push(item._id)
		});

		axios({
			method: 'post',
			url: `${Url}/CRM/Groups/getByIds`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			},
			data: mass_id
		})
		.then((res) => {
			setGroups(res.data)
			Swalclose()
		})
		.catch((error) => {
			onError(error, getGroups)
		})
	};
	
	const getUser = (title) => {

		loading(title, '')

		axios({
			method: 'get',
			url: `${Url}/CRMAccounts/${jwt.decode(localStorage.getItem('tokenID')).id}`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			}
		})
		.then((res) => {
			if (!res.data) {
				localStorage.removeItem('tokenID');
				window.location.replace("/");
			}
			
			setUser(res.data)
			getGroups(res.data)
		})
		.catch((error) => {
			onError(error, getUser)
		})
	};

	useEffect(() => {
		getUser('ÐÐ¾Ð»ÑÑÐµÐ½Ð¸Ðµ Ð´Ð°Ð½Ð½ÑÑ...')
	}, [])

	const [rowKey, setRowKey] = useState([])
	const [GROUP, setGROUP] = useState({
			group: {
					GROUP_NAME: '',
					GLOBAL_SCHEDULE: [
					{date: "00", duration: ['', '']}
					],
					PUPILS: [],
					_id: '3443'
				},
			indexD: 0
		})

	const { Header, Sider } = Layout;
	const { SubMenu } = Menu;

	const [collapsed, setCollapsed] = useState(false)

	const onCollapse = collapsed => {
		setCollapsed(collapsed);
	};

	const SiteLayout = styled.div({

	})
	const Vag = styled.div({
		'.ant-menu': {
			height: '100%',
			position: 'relative'
		},
		'.vag123': {
			position: 'absolute',
			bottom: '0'
		}
	})

	console.log(GROUP, Groups)

	return (
		<Router>
		<Vag>
	      <Layout style={{ minHeight: '100vh' }}>
	        <Sider collapsible collapsed={collapsed} onCollapse={onCollapse}>
	          <div className="logo" />
	          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
	          
			          <Menu.Item key="1" icon={<ClockCircleOutlined />}>
				          <Link to='/'>
				          		Ð¡ÐµÐ³Ð¾Ð´Ð½Ñ
				          </Link>
			          </Menu.Item>
			          <Menu.Item key="2" icon={<CalendarOutlined />}>
				          <Link to='/calendar'>
				          	Ð Ð°ÑÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ
				          </Link>
			          </Menu.Item>
			          <Menu.Item key="3" icon={<UserOutlined />}>
				          <Link to='/pupils'>
				          	Ð£ÑÐµÐ½Ð¸ÐºÐ¸
				          </Link>
			          </Menu.Item>
			          <Menu.Item key="4" icon={<TeamOutlined />}>
				          <Link to='/groups'>
				          	ÐÑÑÐ¿Ð¿Ñ
				          </Link>
			          </Menu.Item>
			          <Menu.Item 
			            	onClick={() => {
			            		localStorage.removeItem('tokenID');
			            		logOut()
			            	}}
			            	key="Logout" className='vag123' icon={<LogoutOutlined />}>
				            <Link to='/'>
				            	ÐÑÑÐ¾Ð´: {user.login}
				            </Link>
			          </Menu.Item>
	          </Menu>
	        </Sider>
	        <Layout className="site-layout">
	          {/*<Header className="site-layout-background" style={{ padding: 0 }}>
	          	<h3>Hello</h3>
	          </Header>*/}

	            <SiteLayout>
		            <Route exact path='/'>
		           		<Today rowKey={rowKey} getUser={getUser} setRowKey={setRowKey} GROUP={GROUP} setGROUP={setGROUP} Groups={Groups}/>
		            </Route>
		            <Route exact path='/calendar'>
		           		<Ð¡alendar Groups={Groups}/>
		            </Route>
		            <Route exact path='/pupils'>
		           		<AllPupils AllPupils={Pupils}/>
		            </Route>
		            <Route exact path='/groups'>
		           		<AllGroups AllGroups={Groups}/>
		            </Route>
	            </SiteLayout>

	        </Layout>
	      </Layout>
	      </Vag>
	    </Router>
    )
}

export default HomePageTutor