import React, {useState, useEffect} from 'react';
import Url from './../../../../url/url.js';
import localStorage_change from './../../../../#localStorage_change.js';
import {swallGood, swallErr} from './../../../../alert/alert.js';

import moment from 'moment';
import { Modal, Button } from 'antd';

// Style
import styled from '@emotion/styled';

const ScheduleForm = ({updateGroup, groupID}) => {

	// Modal
	const [isModalVisible, setIsModalVisible] = useState(false);
	const [loading, setLoading] = useState(false)

	const showModal = () => {
		setIsModalVisible(true);
	};

	const handleCancel = () => {
		setIsModalVisible(false);
	};
	// /Modal

	const axios = require('axios'); // AJAX

	let dataInputs = {
		startDate: '',
		endDate: '',
		duration_Start: '',
		duration_End: '',
		weekDay: []
	};

	let weekDay = {
		one: [],
		two: [],
		three: [],
		four: [],
		five: [],
		six: [],
		zero: []
	}; 

	let schedule = [];

	const FindNeedle = () => {

		let regDay = /([a-zÐ°-ÑÑ]+)/ig,
			regNumber = /([0-9]+)/i;

		let dayNumber = moment(dataInputs.startDate).day();


		let {endDate} = dataInputs;
		let day_end = moment(endDate).format('DD.MM.YYYY');

		let day = moment(dataInputs.startDate).format('DD.MM.YYYY'),
			day_number = moment(day, 'DD.MM.YYYY').day(),
		    inc = 1;

		const PushDay = () => {
			dataInputs.weekDay.forEach((item) => {

				if (item == day_number) {
					let scheduleItem = {
				      	date: day,
				      	duration: [dataInputs.duration_Start, dataInputs.duration_End],
					    status: '',
					    tutor: '',
					    paid: false,
					    title: ''
					};
					schedule.push(scheduleItem);
				}
			});
		};
		PushDay();

		do {
			day = moment(dataInputs.startDate).day(dayNumber + inc).format('DD.MM.YYYY');
			day_number = moment(day, 'DD.MM.YYYY').day();

			PushDay();

			inc++;
		} while (day != day_end);

		axios({
			method: 'post',
			url: `${Url}/CRM/Groups/${groupID}/Schedule`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			},
			data: schedule
		})
		.then((res) => {

			document.querySelectorAll("input").forEach((item) => {item.value = ''});
			setLoading(false)
			setIsModalVisible(false)
			swallGood('Ð Ð°ÑÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ ÑÐ¾Ð·Ð´Ð°Ð½Ð¾', '');
	    	for (let key in weekDay) {
				weekDay[key] = [];
			}
			updateGroup(); // ÐÐ±Ð½Ð¾Ð²Ð¸ÑÑ Ð³ÑÑÐ¿Ð¿Ñ.
		})
		.catch((error) => {
			if (error.response) {

				let {status, data} = error.response;

				if (status == 400) {
					swallErr('ÐÐµ Ð²ÐµÑÐ½Ð¾ Ð²Ð²ÐµÐ´ÐµÐ½Ñ Ð´Ð°Ð½Ð½ÑÐµ');
				}
				if (data.message == 'TOKEN_EXPIRED') {
					localStorage_change(data.token);
					FindNeedle();
				} else if (status == 401) {
					localStorage.removeItem('tokenID');
					window.location.replace("/");
				} else if (status == 500) {
					swallErr('ÐÑÐ¸Ð±ÐºÐ° ÑÐµÑÐ²ÐµÑÐ°');
				}
			}
		})

		console.log(schedule);
	};


	const FormChance = () => {
		setLoading(true)

		for (let key in weekDay) {
			if (weekDay[key][0]) {
				dataInputs.weekDay.push(weekDay[key][1]);
			}
		}
	
		FindNeedle();
	};

	const ScheduleForm = styled.div({
		'.form-check': {
			marginRight: '12px',
			paddingLeft: '100px'
		}
	});

	return (
		<>
			<i onClick={showModal}
					  className="bi bi-calendar2-plus" 
					  style={{fontSize: "26px", color: '#17a2b8', cursor: 'pointer'}}
					  ></i>

			<Modal 
				title="ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ ÑÐ°ÑÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ" 
				visible={isModalVisible} 
				onCancel={handleCancel}
				width={1000}
				footer={[
					<Button key="onCancel1" onClick={handleCancel}>
						ÐÑÐ¼ÐµÐ½Ð°
					</Button>,
					<Button 
						key="onOk1" 
						onClick={FormChance}
						loading={loading}
						type="primary"
						>
						Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ
					</Button>
				]}
				>
				<ScheduleForm>
					<form className="row g-3">
			        	<div className="col-md-6">
						    <label className="form-label">ÐÐµÑÐ¸Ð¾Ð´:</label>
						    <div className="row periud">
						    	<input
						    		onChange={(e) => {dataInputs.startDate = e.target.value}} 
						    		type="date" className="form-control col-5"/>
							    <div className="input-group-text">Ð´Ð¾</div>
	      						<input
	      							onChange={(e) => {dataInputs.endDate = e.target.value}} 
	      							type="date" className="form-control col-5"/>
						    </div>
						</div>
						<div className="col-md-6">
						    <label className="form-label">ÐÑÐµÐ¼Ñ Ð·Ð°Ð½ÑÑÐ¸Ñ:</label>
						    <div className="row periud">
						    	<input
						    		onChange={(e) => {dataInputs.duration_Start = e.target.value}} 
						    		type="time" className="form-control col-5"/>
							    <div className="input-group-text">Ð´Ð¾</div>
	      						<input
	      							onChange={(e) => {dataInputs.duration_End = e.target.value}} 
	      							type="time" className="form-control col-5"/>
						    </div>
						</div>
						<div className="col-12">
							<label className="form-label">ÐÐ½Ð¸ Ð½ÐµÐ´ÐµÐ»Ð¸:</label>
						    <div className="row col-12">
						    	<div className="form-check">
							        <input
							        	onChange={(e) => {
							        		weekDay.one = [e.target.checked, e.target.value]
							        	}} 
							        	className="form-check-input" value='1' type="checkbox"/>
							      	<label className="form-check-label">
							        	ÐÐ½
							      	</label>
							    </div>
							    <div className="form-check">
							        <input
							        	onChange={(e) => {
							        		weekDay.two = [e.target.checked, e.target.value]
							        	}}  
							        	className="form-check-input" value='2' type="checkbox"/>
							      	<label className="form-check-label">
							        	ÐÑ
							      	</label>
							    </div>
							    <div className="form-check">
							        <input
							        	onChange={(e) => {
							        		weekDay.three = [e.target.checked, e.target.value]
							        	}}  
							        	className="form-check-input" value='3' type="checkbox"/>
							      	<label className="form-check-label">
							        	Ð¡Ñ
							      	</label>
							    </div>
							    <div className="form-check">
							        <input
							        	onChange={(e) => {
							        		weekDay.four = [e.target.checked, e.target.value]
							        	}}  
							        	className="form-check-input" value='4' type="checkbox"/>
							      	<label className="form-check-label">
							        	Ð§Ñ
							      	</label>
							    </div>
							    <div className="form-check">
							        <input
							        	onChange={(e) => {
							        		weekDay.five = [e.target.checked, e.target.value]
							        	}} 
							        	className="form-check-input" value='5' type="checkbox"/>
							      	<label className="form-check-label">
							        	ÐÑ
							      	</label>
							    </div>
							    <div className="form-check">
							        <input
							        	onChange={(e) => {
							        		weekDay.six = [e.target.checked, e.target.value]
							        	}}  
							        	className="form-check-input" value='6' type="checkbox"/>
							      	<label className="form-check-label">
							        	Ð¡Ð±
							      	</label>
							    </div>
							    <div className="form-check">
							        <input 
							        	onChange={(e) => {
							        		weekDay.zero = [e.target.checked, e.target.value]
							        	}} 
							        	className="form-check-input" value='0' type="checkbox"/>
							      	<label className="form-check-label">
							        	ÐÑ
							      	</label>
							    </div>
						    </div>
						</div>
			        </form>
			    </ScheduleForm>
			</Modal>
		</>
	);
};

export default ScheduleForm;