import React, {useState, useEffect} from 'react';
import { Menu, Dropdown } from 'antd';

import Url from './../../../../../url/url.js';
import localStorage_change from './../../../../../#localStorage_change.js';
import {swallGood, swallErr} from './../../../../../alert/alert.js';

import Change_Balance from './../../../change-balance/changeBalance.js';
// Style 
import styled from '@emotion/styled';

const Balance = ({balance_item, pageStudent_id, updeteStudent}) => {

	// const axios = require('axios'); // AJAX
	const [visible, setVisible] = useState(false)
	const [balance, setBalance] = useState(+balance_item? +balance_item : 0);

	let incBalance = 0;

	const ChangeBalance = () => { 

		setBalance(balance + +incBalance);

		Change_Balance(incBalance, pageStudent_id, updeteStudent);
	}

	const Span = styled.span({
		borderRadius: '6px',
		fontWeight: '500',
		cursor: 'pointer',
		fontSize: '14px'
	});

	const menu = (
	  <Menu>
	    <Menu.Item>
		   	<input
			    placeholder="ÐÐ¾Ð¿Ð¾Ð»Ð½Ð¸ÑÑ"
			    onChange={(e) => {incBalance = e.target.value}} 
			    type="number" className="text-info form-control col-12"
			   	/>
	    </Menu.Item>
	    <Menu.Item>
		   	<Span
			   	onClick={() => {
			   		setVisible(!visible)
			   		ChangeBalance()
			   	}} 
			   	className="badge bg-warning text-dark">
			   	Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ
		   	</Span>
	    </Menu.Item>
	  </Menu>
	);

	return (
		<div className="balance col-md-12 row">
			<h6 className="calendar badge bg-success text-light">
				{balance}
				<span>â½</span> 
			</h6>
			<Dropdown overlay={menu} visible={visible} trigger={['click']}>
				<i  id="dropdownMenuReference"
					className="bi bi-plus" 
					onClick={() => {setVisible(!visible)}}
					style={{fontSize: "30px", color: '#17a2b8', cursor: 'pointer'}}
					></i>
			</Dropdown>
		</div>
	);
}

export default Balance;