import React, {useState, useEffect} from 'react';
import './form-log-in.css';
import Url from '../../url/url.js';

import {swallErr, Toast} from './../../alert/alert.js';
import withReactContent from 'sweetalert2-react-content';

import styled from '@emotion/styled';

const FormlogIn = ({setUser}) => {

	// let jwt = require('jsonwebtoken');

	const axios = require('axios'); // AJAX

	const [eye, setEye] = useState({
		icons: 'bi-eye-fill',
		type: 'password'
	}); // bi-eye-slash-fill
	const [valuePassword, setValuePassword] = useState(''),
		  [valueLogin, setValueLogin] = useState('');	

	const [disabled, setDisabled] = useState('submit'); 

	// Message   
	const [message, setMessage] = useState('ÐÐ¾Ð³Ð¸Ð½');

	// Style state
	const [inputBorder_L, set_I_Border_L] = useState(''),
	      [inputBorder_P, set_I_Border_P] = useState('');


	let valueChangeL = valueLogin,
	    valueChangeP = valuePassword;

	const showPassword = () => {
		setValuePassword(valueChangeP);
		setValueLogin(valueChangeL);

		if (eye.icons === 'bi-eye-fill') {
			setEye({
				icons: 'bi-eye-slash-fill',
				type: 'text'
			});
		} else {
			setEye({
				icons: 'bi-eye-fill',
				type: 'password'
			});
		}
	}

	// Alert
			const swallGood = () => {
				Toast.fire({
					icon: 'success',
					title: 'ÐÑ ÑÑÐ¿ÐµÑÐ½Ð¾ Ð²Ð¾ÑÐ»Ð¸ Ð² ÑÐ¸ÑÑÐµÐ¼Ñ'
				});
			}
	// \alet

	let opacityBtn = '1';

	if (disabled == 'button') {
		opacityBtn = '0.3';
	}

	const LogIn = styled.div({
		height: '500px',
		button: {
			    fontWeight: 'bold',
			    fontSize: '21px',
			    width: '150px',
			    borderRadius: '19px',
			    transition: '0.3s',
			    borderColor: '#007bff',
			    color: '#000',
			    opacity: `${opacityBtn}`,
			    '&:hover': {
			    	transition: '0.3s',
			    	backgroundColor: '#f7d99f'
				}
		},
		label: {
			fontSize: '24px',
    		fontWeight: '400'
		},
		input: {
			color: '#000',
			fontSize: '20px'
		},
		'input[type=text]': {
			background: 'none'
		},
		'input[type=password]': {
			background: 'none'
		},
		i: {
			cursor: 'pointer'
		},
		'.inputBorder_L': {
			border: `${inputBorder_L}`
		},
		'.inputBorder_P': {
			border: `${inputBorder_P}`
		}
	});
// =============

	let regLogin = new RegExp(/^[\w\d\W]{1,40}$/),
	    regPassword = new RegExp(/^.{1,40}$/);

	// console.log(regPassword.test(text));

	const FormSubmit = async (e) => {

		setValuePassword(valueChangeP);
		setValueLogin(valueChangeL);
		setMessage('ÐÐ¾Ð³Ð¸Ð½');

		e.preventDefault();

		if (!regLogin.test(valueChangeL)) {
			set_I_Border_L('2px solid red');
		} else {
			set_I_Border_L('');
		}
 		if (!regPassword.test(valueChangeP)) {
			set_I_Border_P('2px solid red');
		} else {
			set_I_Border_P('');
		}

		if (regLogin.test(valueChangeL) & regPassword.test(valueChangeP)) {
			setDisabled('button');

			axios({
					method: 'post',
					url: `${Url}/auth`,
					headers: {
						'Content-Type': 'application/json;charset=utf-8'
					},
					data: {
						login: valueChangeL,
						password: valueChangeP
					}
			})
			.then((res) => {
				localStorage.setItem('tokenID', res.data.token)
				// jwt.decode(localStorage.getItem('tokenID')
				
				setValuePassword('');
				setValueLogin('');
				setDisabled('submit');			
				swallGood();
				setUser(valueChangeL);
			})
			.catch((error) => {
				if (error.response) {
					let {status} = error.response;
					if (status == 400) {
						swallErr('ÐÐ¹..', 'ÐÐ¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ Ð½Ðµ ÑÑÑÐµÑÑÐ²ÑÐµÑ');
						set_I_Border_L('2px solid red');
						set_I_Border_P('2px solid red');
						setDisabled('submit');
					} else {
						setDisabled('submit');
					}
				}
			});
		}
	};

	return (
		<LogIn className="LogIn container d-flex align-items-center justify-content-center">
				<form 
					onSubmit={FormSubmit}
					key={'form1'} className="col-md-8 col-12">
					<fieldset>
					  <div className="mb-3">
					    <label htmlFor="exampleInputEmail1" className="form-label">{message}</label>
					    <input
					    	required 
					    	onChange={(e) => {valueChangeL = e.target.value}}
					    	defaultValue={valueLogin} autoComplete="current-password" type="text" className="form-control inputBorder_L" name='login' id="exampleInputEmail1"/>
					  </div> 
					  <div className="mb-3">
					    <label htmlFor="exampleInputPassword1" className="form-label">ÐÐ°ÑÐ¾Ð»Ñ</label>
					    <input
					    	required
					    	onChange={(e) => {valueChangeP = e.target.value}} 
					    	defaultValue={valuePassword} autoComplete="current-password" type={`${eye.type}`} className="form-control inputBorder_P" name="password" id="exampleInputPassword1"/>
					  	<div className="password-control">
					  		<div className="d-flex justify-content-end">
					  			<i onClick={showPassword} className={`bi ${eye.icons}`} style={{fontSize: '28px', color: '#F5896C'}}></i>
					  		</div>
					  	</div>
					  </div>
					  <button 
					  	type={disabled} className="btn">ÐÐ¾Ð¹ÑÐ¸</button>
				  	</fieldset>
				</form>
		</LogIn>
	);
};

export default FormlogIn;