						      <InputMask  mask="Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°Ð°" maskChar={null} formatChars={formatNSM} autoComplete='off' onChange={(e) => {dataInput.surname = e.target.value}} required type="text" name="surname" className={`form-control${borderInput}`} id="inputSirname"/>
