 import React, {useState, useEffect} from 'react';
import Url from './../../../url/url.js';

import moment from 'moment';
// import localStorage_change from './../../../#localStorage_change.js';
// import {swallGood, swallErr} from './../../../alert/alert.js';

// Style 
import styled from '@emotion/styled';

const Schedule = ({GLOBAL_SCHEDULE}) => {

	const SchedulE = styled.div({
		padding: '0',
		marginBottom: '15px',
		'.boxS': {
			    overflow:'auto',
    			maxHeight: '200px',
		},
		'.dataNumber': {
			margin: '0 3px 0 0',
		    fontSize: '15px',
		    fontWeight: '600',
		    width: '48px'
		}
	});

	if (!GLOBAL_SCHEDULE[0]) {
		return (
			<SchedulE  className="col-12">
				<h5>Ð Ð°ÑÐ¿Ð¸ÑÐ°Ð½Ð¸Ñ Ð½ÐµÑ...</h5>
			</SchedulE>
		);
	}

	// GLOBAL_SCHEDULE.duration = ['0', '0'];

	let SpanDay = GLOBAL_SCHEDULE.map((item) => {

		let date = moment(item.date, 'DD.MM.YYYY').format('DD.MM');

		return (
			<span key={item.date} className="badge bg-light text-dark dataNumber">{date}</span>
		);
	});

	return (
		<SchedulE className="col-12">
			<h5>{GLOBAL_SCHEDULE[0].duration[0]} Ð´Ð¾ {GLOBAL_SCHEDULE[0].duration[1]}</h5>
			<div className="boxS">
				{SpanDay}
			</div>
		</SchedulE>
	);
};

export default Schedule;