import React, {useState, useEffect} from 'react';

import Url from './../../../../url/url.js';
import localStorage_change from './../../../../#localStorage_change.js';
import {swallErr} from './../../../../alert/alert.js'

import ReturnAge from './../../return-age/returnAge.js';
import PupilsSandTime from './../../group/group-page/PupilsSandTime/PupilsSandTime.js';
import InfoTable from './Info-table/InfoTable.js';
import PayHistory from './payment-history/paymentHistory.js';
import DrawerChat from './chat/drawer-chat.js'

import CheckRole from './../../CheckRole/CheckRole.js'
// Block
import Balance from './balance/balance.js';

// Style 
import styled from '@emotion/styled';

const PageStudent = ({pageStudent_id, setUrlPage}) => {

	let jwt = require('jsonwebtoken');

	const [dataStudent, setDataS] = useState({
	    _id: "60ba505dc12d3b9783c51d0f",
	    name: "ÐÐ°Ð³ÑÑÐ·ÐºÐ°...", 
		surname: "", 
		midname: "", 
		gender: "", 
		age: "2004-12-07",
		phone: "",
		parentPhone: "", 
		discord: "",
		balance: 0,
		localSchedule: {},
		groups: [],
		paymentHistory: [
			{type: 1, date: "", amount: 0, issuer: "", subscription: null},
			{type: 0, date: "", amount: 0, issuer: "", subscription: null}
		],
		notes: []
	});

	const {_id, notes, groups, name, surname, midname, gender, age, phone, parentPhone, discord, balance, localSchedule, paymentHistory} = dataStudent;

	const axios = require('axios'); // AJAX

	const getStudent_Id = () => {
		axios({
			method: 'get',
			url: `${Url}/CRM/Pupils/${pageStudent_id}`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			}
		})
		.then((res) => {
			setDataS(res.data);
		})
		.catch((error) => {
			if (error.response) {
				let {status, data} = error.response;

				if (status == 404) {
					swallErr('404', 'Ð¡ÐµÑÐ²ÐµÑ Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½')
				} else if (status == 401) {

					if (data.message == 'TOKEN_EXPIRED') {

						localStorage_change(data.token);
						getStudent_Id();
					} else {
						localStorage.removeItem('tokenID');
						window.location.replace("/");
					}
				}	
			}
		})
	}

	useEffect(() => {
		getStudent_Id();
	}, []);

	let arrIdSch = [];
	for (let key in localSchedule) {
		arrIdSch.unshift(key);
	}

	let groups_id = [];

	groups.forEach((item) => {
		groups_id.push(item.GROUP_NAME);
	}); 

	let PupilsSandTimeGroup = arrIdSch.map((id, i) => {
		console.log(dataStudent)

		if (i < groups_id.length) {
			return <PupilsSandTime key={id} setUrlPage={setUrlPage} pageInfo={'group'} itemG={dataStudent} dataGroup={id} getGroup_Id={getStudent_Id} name={groups_id[i]}/>
		}

	});
	
	// Style

	const breakpoints = [767],
		  mq = breakpoints.map(
			bp => `@media (max-width: ${bp}px)`
		);

	const StudentPage = styled.div({
		marginTop: '0px',
		margin: '0 auto',
		'.pad': {
			padding: '0px',
			paddingRight: '10px',
			[mq[0]]: {
				paddingRight: '0px'
			}
		},
		'.infoLog': {
			marginBottom: '10px',
			padding: '0',
			[mq[0]]: {
				paddingBottom: '10px'
			},
			'.calendar': {
				margin: 'auto 0',
    			marginRight: '10px',
    			fontSize: '20px',
    			span: {
    				paddingLeft: '6px'
    			}
			}
		},
		'.group-name': {
			borderBottom: '1px solid #0dcaf0',
			padding: '0px',
			[mq[0]]: {
				borderRight: 'none'
			}
		},
		'.balance': {
			h5: {
				fontSize: '1.25rem'
			}
		},
		'.Button-Drawer': {
			position: 'absolute',
			right: '0',
			top: '8px'
		},
		'.ulBox': {
			borderRadius: '15px',
			padding: '0px',
		},
		'.editing': {
			background: '#fff',
    		borderRadius: '10px 10px 0 0',
    		i: {
    			marginLeft: '95%',
    			cursor: 'pointer'
    		}
		},
		li: {
			backgroundColor: 'none',
			span: {
				fontSize: '16px',
				margin: '0 5px 5px 0'
			}
		}
	});

	const TablePupils = styled.div({
		background: '#fff',
	    margin: '10px 0',
	    padding: '10px',
	    borderRadius: '15px',
		'& > h3': {
			fontSize: '22px'
		}
	});

	return (
		<StudentPage className="container row col-12">
				<div className="col-12 col-md-6 pad">
					<div className="infoLog align-self-start col-md-12 col-12">
						<div className="group-name col-12 col-lg-12">
							<h3>{name}</h3>
							<DrawerChat notes={notes} update={getStudent_Id} _id={pageStudent_id}/>
						</div>
						<Balance balance_item={balance} pageStudent_id={pageStudent_id} updeteStudent={getStudent_Id}/>
					</div>
					<PayHistory pay_History={paymentHistory}/>
				</div>
				<div className="col-md-6 ulBox">
					<InfoTable dataStudent={dataStudent} updetePage={setDataS}/>
				</div>
				<TablePupils className="container col-12">
					<h3 className="badge bg-warning">ÐÑÑÐ¿Ð¿Ñ:</h3>
					{PupilsSandTimeGroup}
				</TablePupils>
		</StudentPage>
	);
};

export default PageStudent;
