import React, {useState, useEffect} from 'react';
import Url from './../../url/url.js';
import localStorage_change from './../../#localStorage_change.js';

import {Link} from "react-router-dom";

import { Input, AutoComplete } from 'antd';
import { UserOutlined } from '@ant-design/icons';

import styled from '@emotion/styled';

const SearchAll = ({setPageStudent, setPageGruop}) => {

  const axios = require('axios'); // AJAX

  const [Data, setData] = useState([])
  const [Value, setValue] = useState('')

  useEffect(() => {
    document.getElementById('Search').focus();
  });

  const onSearch = (value) => {

    let localUrl = encodeURI(value)
   
    axios({
      method: 'get',
      url: `${Url}/CRM/Search/autocompletion?query=${localUrl}`,
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
      }
    })
    .then((res) => {
      setValue(value)
      setData(res.data)
    })
  }

  const renderTitle = (title: string) => (
    <span>
      {title}
    </span>
  );


  const renderItem = (title: string, obj) => {

    let updete = () => {},
        url = `/student/${obj._id}`

    if (obj.type == 'pupil') {
      updete = () => {setPageStudent(obj._id)}
      url = `/student/${obj._id}`
    } else {
      updete = () => {setPageGruop(obj._id)}
      url = `/group/${obj._id}`
    }

    return ({
      value: title,
      label: (
          <Link
            className="link-info"
            onClick={updete} 
            to={url}
            style={{
              display: 'flex',
              justifyContent: 'space-between'
            }}>

            {title}
          </Link>
        ),
    })
  };


  const options = [
    {
      label: renderTitle('Ð£ÑÐµÐ½Ð¸ÐºÐ¸'),
      options: [],
    },
    {
      label: renderTitle('ÐÑÑÐ¿Ð¿Ñ'),
      options: [],
    }
  ];

  Data.forEach(item => {

    if (item.type == 'pupil') {

      let {surname, name, midname} = item
      options[0].options.push(renderItem(`${surname} ${name} ${midname}`, item))

    } else {
      let {name} = item
      options[1].options.push(renderItem(name, item))
    }
  })

  let typingTimer                //timer identifier
  let doneTypingInterval = 800  //time in ms (5 seconds)

  const ononSearchInput = (text) => {
    clearTimeout(typingTimer)
    if (text) {
        typingTimer = setTimeout(() => {
          onSearch(text)
        }, doneTypingInterval);
    }
  }


  const Box = styled.div({
    height: '30px',
    input: {
      height: '30px'
    },
    // margin: 'auto 27px',
    '.ant-input-search-button': {
      paddingBottom: 'none'
    }
  })


  return (
      <Box>
        <AutoComplete
            dropdownClassName="certain-category-search-dropdown"
            dropdownMatchSelectWidth={500}
            // style={{ width: 250 }}
            options={options}
            defaultOpen={() => {
              if (Value) {
                return true
              } else {
                return false
              }
            }} // Ð¾ÑÐºÑÑÐ²Ð°ÐµÑ Ð¿Ð¾Ð¸ÑÐº Ð¿ÑÐ¸ ÑÐµÐ½Ð´ÐµÑÐ¸Ð½Ð³Ðµ
            defaultValue={`${Value}`}
            onSearch={(text) => {
              ononSearchInput(text)
            }}
            id='Search'>

          <Input size="large" placeholder="ÐÐ¾Ð¸ÑÐº Ð¿Ð¾ CRM" />
        </AutoComplete>
      </Box>
    );
}

export default SearchAll