import React, {useState, useEffect} from 'react';
import { Modal, Button } from 'antd';

import Url from './../../../../url/url.js';
import localStorage_change from './../../../../#localStorage_change.js';

import {loading, Swalclose} from './../../../../alert/alert.js';

import moment from 'moment';

//Style
import styled from '@emotion/styled';

const AddStydent_Group = ({PupilsGroup, http, title, pageId, update, schedule}) => {

	// Modal
	const [isModalVisible, setIsModalVisible] = useState(false);

	const showModal = () => {
		setIsModalVisible(true);
	};

	const handleCancel = () => {
		setIsModalVisible(false);
	};
	// /Modal

	const axios = require('axios'); // AJAX

	const [PUPILS, setPupils] = useState([
		{
			age: "",
			gender: "",
			midname: "",
			name: "",
			surname: "",
			_id: "60cb81c09b3f46001f5ce4d2"
		}
	]);

	const getPupils = () => {
		axios({
			method: 'post',
			url: `${Url}${http}/find?limit=150&offset=0`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			}
		})
		.then((res) => {

			let {data} = res;

			let arr = [],
			    itn = 0;

			if (!PupilsGroup.length) {
				setPupils(data);
				return ;
			}    

			data.forEach((itemS, i) => {

				// let copu = true;


				if (itemS._id != PupilsGroup[itn]._id) {
					arr.push(itemS);
				}

				if (PupilsGroup.length - 1 > itn) {
					itn++;
				} 
			})
			setPupils(arr);
		})
	};

	const changeSchedulePupil = (idPupil) => {

		let newSchedule = [];

		schedule.forEach((item) => {

			let day = moment(item.date, 'DD.MM.YYYY').format('YYYY-MM-DD'),
				time_day = moment().diff(day, 'day');

			if (time_day <= 0) {
				newSchedule.push(item);
			}
		})

		axios({ 
			method: 'put',
			url: `${Url}/CRM/Groups/${pageId}/Pupils/${idPupil}/Schedule`,
			headers: {
				'Content-Type': 'application/json;charset=utf-8',
				'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
			},
			data: newSchedule
		})
		.then((res) => {
			update();
			Swalclose();
		})
	}
	const addPUPILS_Group = (data, idPupil) => {
		axios({
				method: 'post',
				url: `${Url}/CRM/Groups/${pageId}/Pupils`,
				headers: {
					'Content-Type': 'application/json;charset=utf-8',
					'Authorization': `Bearer ${localStorage.getItem('tokenID')}`
				},
				data: data
			})
			.then((res) => {
				changeSchedulePupil(idPupil);
			})
			.catch((error) => {
				if (error.respons) {
					console.log(error);
				}
			})
	}
 
	useEffect(() => {
		getPupils()
	}, []);

	let PUPILS_search_Li = PUPILS.map((item) => {

		const choice = () => {

			loading('Ð£ÑÐµÐ½Ð¸Ðº Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑÑÑ', 'ÐÐ¾Ð¶Ð°Ð»ÑÐ¹ÑÑÐ° Ð¿Ð¾Ð´Ð¾Ð¶Ð´Ð¸ÑÐµ');

			addPUPILS_Group([item._id], item._id);
		};

		const Li = styled.li({
			span: {
				cursor: 'pointer'
			}
		});

		return (
			<Li key={item._id}
				onClick={choice}
				className="list-group-item">
				<span className="badge bg-warning text-dark">{item.surname} {item.name}</span>
			</Li>
		);
	});

	const Span = styled.span({
		cursor: 'pointer'
	});

	return (
		<>
		<Span 
			className="btn " 
			onClick={showModal}
			>
		  <i className="bi bi-person-plus-fill" style={{color: '#ffc107', fontSize: '24px'}}></i>
		</Span>

		<Modal
		    title={`ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ ${title}`}
		    visible={isModalVisible}
		    onCancel={handleCancel}
		    width={600}
		    footer={[
		        <Button key="onCancel1" onClick={handleCancel}>
		            ÐÑÐ¼ÐµÐ½Ð°
		        </Button>
		    ]}
		>
			<div className="row addPUPILS">
			    <div className="col-12 input-group">
			        <input
			            id="fort"
			            onChange={() => {}}
			            autoComplete="off"
			            type="search"
			            className="form-control"
			            name="q"
			            placeholder="ÐÐ¾Ð¸ÑÐº Ð½Ð°ÑÐ¾Ð´Ð¸ÑÑÑÑ Ð² ÑÐ°Ð·ÑÐ°Ð±Ð¾ÑÐºÐµ..."
			        />

			        <ul className="col-12 list-group list-group-flush">
			            {PUPILS_search_Li}
			        </ul>
			    </div>
			</div>
		</Modal>
		</>
	);

};

export default AddStydent_Group;