import Url from './../../../url/url.js';
import localStorage_change from './../../../#localStorage_change.js';
import {swallGood, swallErr} from './../../../alert/alert.js';

const Change_Balance = (sym, pupil_id, updeteStudent, Change = () => {}, subscription, group_id) => {
	
	const axios = require('axios'); // AJAX

	axios({
		method: "post",
		url: `${Url}/CRM/Pupils/${pupil_id}/Payment?amount=${+sym}&subscription=${subscription}&group_id=${group_id}`, // amount&subscription&group_id 
		headers: {
			"Content-Type": "application/json;charset=utf-8",
			Authorization: `Bearer ${localStorage.getItem("tokenID")}`,
		},
	})
	.then((res) => {
		Change();
		updeteStudent();
	})
	.catch((error) => {
		if (error.response) {
			if (error.response.status == 401) {
				if (error.response.data.message == "TOKEN_EXPIRED") {
					localStorage_change(error.response.data.token);
				} else {
					localStorage.removeItem("tokenID");
					window.location.replace("/");
				}
			}
			updeteStudent();
			swallErr("ÐÐµ ÑÐ´Ð°Ð»Ð¾ÑÑ Ð¿Ð¾Ð¿Ð¾Ð»Ð½Ð¸ÑÑ Ð±Ð°Ð»Ð°Ð½Ñ", "ÐÐ¾Ð¿ÑÐ¾Ð±ÑÐ¹ÑÐµ ÑÐ½Ð¾Ð²Ð°");
		}
	});

}

export default Change_Balance;